.card {
  display: flex;
  flex-wrap: wrap;
}

.card-item {
  width: 300px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin: 18px;
}

.favouritevideo-card-item {
  width: 400px;
  height: 300px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin: 18px;
}

.photographer {
  color: #fe5000;
  text-decoration: none;
  font-weight: 500;
}

.photo {
  width: 300px;
  height: 400px;
}

.content {
  padding: 20px;
  text-align: center;
}
